@font-face {
  font-family: "Inconsolata";
  font-display: swap;
  src: url("/_assets/fonts/Inconsolata/Inconsolata-VariableFont_wdth-wght.woff2") format("woff2"), url("/_assets/fonts/Inconsolata/Inconsolata.woff") format("woff"), url("/_assets/fonts/Inconsolata/Inconsolata-VariableFont_wdth-wght.ttf") format("truetype");
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.loader {
  display: none;
}

.layout {
  opacity: 1;
}

@media (prefers-color-scheme: light) {
  body {
    background-color: rgb(232, 221, 235);
    color: rgb(63, 48, 48);
    font-weight: 500;
  }
  .feed-links {
    background-color: rgb(246, 240, 248);
  }
  .u-photo {
    filter: drop-shadow(5px 5px 15px rgba(199, 137, 235, 0.08)) drop-shadow(-5px -5px 15px rgba(199, 137, 235, 0.08));
  }
  .h-entry {
    background-color: ghostwhite;
    box-shadow: 5px 5px 15px rgba(199, 137, 235, 0.08), -5px -5px 15px rgba(199, 137, 235, 0.08);
  }
  a {
    color: rgb(88, 24, 56);
  }
  a:hover {
    color: white;
    background-color: rgb(160, 120, 155);
  }
}
@media (prefers-color-scheme: dark) {
  body {
    background-color: black;
    color: white;
    font-weight: normal;
  }
  .notes-format header {
    color: rgb(223, 211, 211);
  }
  .feed-links {
    background-color: rgb(52, 47, 54);
  }
  .u-photo {
    filter: drop-shadow(5px 5px 15px rgba(128, 0, 107, 0.4)) drop-shadow(-5px -5px 15px rgba(128, 0, 107, 0.4));
  }
  .h-entry {
    background-color: rgb(22, 21, 21);
    border-color: rgb(68, 60, 60);
    box-shadow: 5px 5px 15px rgba(128, 0, 107, 0.4), -5px -5px 15px rgba(128, 0, 107, 0.4);
  }
  a {
    color: rgb(255, 166, 181);
  }
  a:hover {
    color: black;
    background-color: rgb(255, 166, 181);
  }
}